:root {
  --font-size-mobile-tablet: 12px;
  --line-height-mobile-tablet: 1.33:
  --font-size-desktop: 20px;
  --line-height-desktop: 1.2;
  --main-black-color: #000000;
  --main-accent-color: #FF6B08;
  }
  
  body{
  font-family: Roboto, sans-serif;
  
  }
.container {
  max-width: 480px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;

  @include mq(tablet) {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @include mq(desktop) {
    max-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

