ight:1.14;color:#545454}.footer__first{text-align:center}.title-first{margin-bottom:10px}@media screen and (min-width: 768px){.title-first{margin-bottom:0}}.footer__second{display:flex;flex-direction:row;gap:10px;margin-left:auto;margin-right:auto}@media screen and (min-width: 768px){.footer__second{margin-left:0;margin-right:0}}.visually-hidden{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}
