///////////////////////////////

.modal-group__title {
  margin-top: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.17;

  text-transform: uppercase;

  @include mq(tablet) {
    margin-top: 0;
  }

  @include mq(desktop) {
    font-size: 30px;
  }
}
.modal-group__img-wrapper {
  @include mq(tablet) {
    width: 264px;
    height: auto;
    flex-shrink: 0;
  }

  @include mq(desktop) {
    width: 375px;
  }
}
.modal-group__img {
  border-radius: 5px;
  // background-size: contain;

  @include mq(desktop) {
    width: 100%;
  }
}
.modal-group__wrapper {
  @include mq(tablet) {
    margin-left: 68px;
  }
  @include mq(desktop) {
    margin-left: 16px;
  }
}

.table {
