  line-height: var(--line-height-mobile-tablet);
  font-weight: 500;
}

.gallery-text__rating {
}

.table-text-orange {
  padding: 1px 6px;
  margin-left: 0;
  display: inline-block;
}

.table-text-grey {
  padding: 1px 6px;
  margin-left: 0;
  display: inline-block;
