.modal-group__about-title {
  margin-bottom: 8px;
  margin-top: 20px;

  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #000000;
}
.modal-group__about-text {
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  color: #000000;
}
.modal-group__btns {
  display: flex;
