}

.watched-btn {
  background-color: var(--main-accent-color);
  color: #ffffff;

  padding: 6px 27px;
  text-transform: uppercase;
  border-radius: 5px;

  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;

  @include mq(tablet) {
    padding: 14px 9px;
  }
}
.queue-btn {
  border: 1px solid #000000;
  background-color: transparent;

  padding: 6px 27px;
  text-transform: uppercase;
  border-radius: 5px;

  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  @include mq(tablet) {
    padding: 14px 9px;
  }
}

.modal-group__close-icon {
  fill: black;
}
